import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Home extends Component {

    render(){
        return(
            <div className = "filter-button">
                <div className="buttons">
                    <Link to = "/filter1"> <button> FILTER 1 </button> </Link> <br />
                    <Link to = "/filter2"> <button> FILTER 2 </button> </Link> <br />
                    <Link to = "/filter3"> <button> FILTER 3 </button> </Link> <br />
                    <Link to = "/filter4"> <button> FILTER 4 </button> </Link> <br />
                </div>
            </div>
        );
    }
}

export default Home;