import React, { Component } from "react";
import Filter2Result from "./Filter2Result";
import Filter2Input from "./Filter2Input";


class Filter2 extends Component {

    state = {
        data: null
    }

    constructor(props){
        super(props);

        this.setData = this.setData.bind(this);
    }

    setData(data){

        this.setState({data: data});

    }


    render(){

        return(
            <div className = "filter">
                
                {
                    this.state.data == null ?
                        <Filter2Input setData = {this.setData}/> 
                    :
                        <div>
                            <Filter2Input setData = {this.setData}/> 
                            <Filter2Result data = {this.state.data} />
                        </div>
                }    

            </div>
        );
    }
}

export default Filter2;