import React, { Component } from "react";

class Filter4Input extends Component {

    state = {
        data: []
    }

    constructor(props){
        super(props);

        this.apply = this.apply.bind(this);
    }

    apply(){

        this.state.data.push(document.getElementById("date1").value);
        this.state.data.push(document.getElementById("date2").value);
    }

    render(){

        return(
            <div>

                <h1 id = "filter1">FILTER 4</h1>

                <form className = "form">
                    <label htmlFor = "brand" className = "label">Date From: </label>
                    <label htmlFor = "brand" className = "label">Date To: </label> <br />
                    <input type = "text" className = "input" name = "brand" placeholder = "dd-mm-yy" id = "date1" />
                    <input type = "text" className = "input" name = "brand" placeholder = "dd-mm-yy" id = "date2" />

                    <button id = "apply"
                        onClick = {() =>  { this.apply();
                            this.props.setData(this.state.data);   
                        }}> APPLY
                    </button>
                </form>

            </div>
        );
    }
}

export default Filter4Input;