import React, { Component } from "react";
import Filter1Result from "./Filter1Result";
import Filter1Input from "./Filter1Input";

class Filter1 extends Component {

    state = {
        data: null
    }

    constructor(props){
        super(props);

        this.setData = this.setData.bind(this);
    }

    setData(data){
        this.setState({data: data});
    }


    render(){

        return(
            <div className = "filter">                
                {
                    this.state.data == null ?
                        <Filter1Input setData = {this.setData}/> 
                    :
                        <div>
                            <Filter1Input setData = {this.setData}/> 
                            <Filter1Result data = {this.state.data} />                            
                        </div>
                } 
            </div>
        );
    }
}

export default Filter1;