import React, { Component } from "react";
import Filter4Result from "./Filter4Result";
import Filter4Input from "./Filter4Input";

class Filter4 extends Component {

    state = {
        data: null
    }

    constructor(props){
        super(props);

        this.setData = this.setData.bind(this);
    }

    setData(data){

        this.setState({data: data});

    }


    render(){

        return(
            <div className = "filter">
                
                {
                    this.state.data == null ?
                        <Filter4Input setData = {this.setData}/> 
                    :
                        <div>

                            <Filter4Input setData = {this.setData}/> 
                            <Filter4Result data = {this.state.data} />
                            
                        </div>
                }    

            </div>
        );
    }
}

export default Filter4;