import React, { Component } from "react";

class Filter2Input extends Component {

    state = {
        data: []
    }

    constructor(props){
        super(props);

        this.apply = this.apply.bind(this);
    }

    apply(){

        this.state.data.push(document.getElementById("brand").value);

    }

    render(){

        return(
            <div>

                <h1 id = "filter1">FILTER 2</h1>

                <form className = "form">
                    <label htmlFor = "brand" className = "label">Brand Name: </label> <br />
                    <input type = "text" className = "input" name = "brand" placeholder = "Enter Brand Name" id = "brand" />

                    <button id = "apply"
                        onClick = {() =>  { this.apply();
                            this.props.setData(this.state.data);   
                        }}> APPLY
                    </button>
                </form>

            </div>
        );
    }
}

export default Filter2Input;